body {
    font-family: $family-sans-serif;
    margin: 0;
    padding: 0; }

.App {
    padding-bottom: 50px; }
.columns {
    display: flex;
    flex-wrap: wrap;
    &.is-centered {
        justify-content: center; }
    &.buttons {
        border: 1px solid #000; }
    .column {
        display: block;
        flex: none;
        &.is-one-quarter {
            width: 25%; }
        &.is-one-third {
            width: 33.33%; }
        &.is-three-quarter {
            width: 75%; }
        &.calculator {
            width: 50%;
            @media screen and (min-width: 768px) {
                width: 30%; }
            @media screen and (max-width: 480px) {
                width: 80%; } } } }

.title {
    padding-top: 20px; }

.Numbers,
.Operators,
.Equal {
    button {
        width: 100%;
        padding: 30% 0;
        font-size: 30px;
        border: 1px solid #000;
        height: auto;
        box-sizing: border-box;
        border-radius: 0;
        color: #fff;
        outline: none;
        cursor: pointer;
        @media screen and (max-width: 480px) {
            font-size: 20px; } } }

.Numbers {
    button {
        background: #8a4d76;
        &:active,
        &:hover {
            background: #82486F; } } }

.Operators {
    button {
        background: #48c774;
        &:active,
        &:hover {
            background: #44ba6b; } } }

.Equal {
    button {
        background: #f14668;
        height: 100%;
        &:active,
        &:hover {
            background: #ed4261; } } }

.Display {
    height: 80px;
    font-size: 30px;
    line-height: 80px;
    background: rgba(0,0,0,0.5);
    color: #fff;
    padding: 0 20px;
    border-radius: 5px 5px 0 0;
    text-align: right;
    overflow: hidden;
    white-space: nowrap; }
